import {Component, OnInit} from '@angular/core';
import {Chart} from 'angular-highcharts';
import {DataService} from '../data.service';

@Component({
  selector: 'app-charts',
  templateUrl: './charts.component.html',
  styleUrls: ['./charts.component.css']
})
export class ChartsComponent implements OnInit {

  chart = new Chart({
    chart: {
      height: 300,
      width: 400,
    },
    xAxis: {
      allowDecimals: false
    },

    plotOptions: {
      series: {
        pointStart: 100
      }
    },

    series: []
  });
  points: number[];

  constructor(private dataService: DataService) {
  }

  ngOnInit() {
    this.getSeries();
  }

  onDrop($event) {
    this.chart.addSerie({
      data: $event.dragData
    });
  }

  newChart(){
    for (let i = this.chart.ref.series.length; i >= 0; i--) {
      this.chart.removeSerie(i);
    }
    this.getSeries();
  }

  getSeries(): void {
    this.points = this.dataService.getData();
    this.chart.addSerie({
      data: this.points
    });
  }

  clear(): void {
    for (let i = this.chart.ref.series.length; i > 0; i--) {
      this.chart.removeSerie(i);
    }
  }
}



